<!--

    
    1.vue2
    2.px2rem sassrempx

    . Attributes
        show {Boolean} false,  

        title {String} slot

        icon {}

        lineStyle {Object} styled titleslotlineH, color :lineStyle="{lineH: '0px'}"

        toastShowStyle {Object}  widthborder-radius

    .Events
        closeToast   v-on$on vue

    .slot
        1. title
        2. icon  title
        3. containerslot

    . 
        1.for
-->


<template>
  <div class="toast"
       v-show="show"
       transition="toast-scale"
       @touchmove.prevent
       @scroll.prevent>
    <!--  -->
    <div class="shadow"></div>

    <!--  -->
    <div class="parent-box">
      <div class="toast-show" :style="toastShowStyle">

        <!--  -->
        <div class="title">
          <slot name="title">
            <div class="title-h">
              <h1 class="title-name" v-if="title">{{title}}</h1>
              <span class="close-icon" @click="closeToast()">
                                <slot name="icon">
                                    <span class="x">X</span>
                                </slot>
                            </span>
            </div>
            <!--line-->
            <div :style="{height: lineStyle.lineH, backgroundColor: lineStyle.color, width: '100%'}"></div>
          </slot>
        </div>


        <!--  -->
        <div  class="container">
          <slot>
            <div></div>
          </slot>
          <slot name="container"></slot>
        </div>

        <!-- footer -->
        <div>
          <slot name="footer">

          </slot>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'toast',
    props: {
      show: {
        default: false,
        type: Boolean
      },
      title: {
        default: '',
        type: String
      },
      icon: {
        default: '',
        type: String
      },
      lineStyle: {
        default: () => {
          return {
            lineH: '1px',
            color: '#ff4800'
          }
        },
        type: Object
      },
      toastShowStyle: {
        default: () => {
          return {
            width: '40%',
            borderRadius: '0px'
          }
        },
        type: Object
      }
    },
    data () {
      return {
        isShow: '',
      }
    },
    watch: {
      show (me) {
        this.isShow = me
      }
    },
    computed: {},
    methods: {
      closeToast () {
        this.$emit('closeToast')
      }
    },
    components: {},
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  /*html,body { overflow: hidden; }*/
  .modal-scale-transition {
    transition: transform
    /*opacity .6s ease;*/
  }

  .modal-scale-enter,
  .modal-scale-leave {
    opacity: 0.6;
  }

  .modal-scale-enter {
    transform: scale(1.1);
  }

  .modal-scale-leave {
    transform: scale(0.8);
  }

  .toast {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color: black;
    z-index: 999;
    /*overflow: hidden;*/
    .shadow {
      background: rgba(0, 0, 0, .6);
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    .parent-box {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .toast-show {
      background: #fff;
      border-radius: 10px;
      max-height: 100%;
      overflow-x: hidden;
      /*overflow-y: hidden;*/
      .title {
        position: relative;
        text-align: center;
        .title-h {
          height: 40px
        }
        .title-name {
          color: black;
          font-size: 16px;
          padding: 10px;
          /*border-bottom: 1px solid #ff4800;*/
        }
        .close-icon {
          position: absolute;
          top: 0px;
          right: 0px;
          background: #fff;
          .x {
            display: inline-block;
            padding: 3px 6px;
            border: 1px solid #eee;
            font-size: 16px;
          }
        }
      }
      .container {
        padding: 20px;
      }
    }
  }
</style>



