<template>
  <div>
    <content-title
      title=""
      tips="">

    </content-title>
    <content-c>
      <ul class="list">
        <li v-for="msg in msgList">
          <div class="item" :class="{'warn-bg': !msg.programme, 'suc-bg': msg.programme}">
            <i class="sprites" :class="{'icon-warn': !msg.programme, 'icon-succ': msg.programme}"></i>
            <div class="center">
              <p class="time">{{getTimeFormat(msg.sendTime)}}</p>
              <p class="com-a-line cont">{{msg.msg}}</p>
            </div>
            <span v-if="!msg.programme" class="done" @click="processedHandle(msg)"></span>
            <span v-else class="detail" @click="detailHandle(msg)"></span>
          </div>
        </li>


        <!--<li class="item warn-bg">-->
        <!--<i class="sprites icon-warn"></i>-->
        <!--<div class="center">-->
        <!--<p class="time">2017-02-21 18:34</p>-->
        <!--<p class="com-a-line cont"></p>-->
        <!--</div>-->
        <!--<span class="done" @click="processedHandle"></span>-->
        <!--</li>-->
        <!--<li class="item suc-bg">-->
        <!--<i class="sprites icon-succ"></i>-->
        <!--<div class="center">-->
        <!--<p class="time">2017-02-21 18:34</p>-->
        <!--<p class="com-a-line cont"></p>-->
        <!--</div>-->
        <!--<span class="detail" @click="detailHandle"></span>-->
        <!--</li>-->
      </ul>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="msgListTotal">
          </el-pagination>
        </div>
      </div>
    </content-c>

    <!---->
    <com-dialog
      :show="processedDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="msg-dialog">
        <h3></h3>
        <el-input
          type="textarea"
          :rows="4"
          placeholder=""
          v-model="form.problem">
        </el-input>
        <h3></h3>
        <el-input
          type="textarea"
          :rows="4"
          placeholder=""
          v-model="form.solution">
        </el-input>
      </div>

      <div slot="title" class="com-dialog-title t-2">
        
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #DB0000; border-color: #DB0000;" type="primary"
                   @click="saveProcess">
        </el-button>
      </div>
    </com-dialog>

    <!---->
    <com-dialog
      :show="msgDetailDialogVisible"
      title=""
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="msg-dialog">
        <h3></h3>
        <p>{{currentMsg.bewrite}}</p>
        <h3></h3>
        <p>{{currentMsg.programme}}</p>
      </div>


      <div slot="title" class="com-dialog-title t-1">
        
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #45C5C0; border-color: #45C5C0;" type="primary"
                   @click="closeDialog">
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import comDialog from '../../../components/toast/dialog.vue'
  import { mapState, mapActions } from 'vuex'
  import { msgType } from '../../../utils/baseConfig'
  import { formatDateTime } from '../../../utils/utils'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 
        pageSizes: [10, 20],
        msgDetailDialogVisible: false,
        processedDialogVisible: false,
        form: {
          problem: '',
          solution: '',
        },
        currentMsg: {},
      }
    },
    computed: {
      ...mapState('message', [
        'msgList',
        'msgListTotal',
        'feedbackStatus',
      ]),
    },
    watch: {
      feedbackStatus (d) {
        if (d) {
          this.ac_msgList({
            entity: {typeId: msgType.warnId},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.closeDialog()
            this.$message({
              type: 'success',
              message: ''
            })
          })
        }
      }
    },
    methods: {
      ...mapActions('message', [
        'ac_msgList',
        'ac_msgFeedback',
      ]),
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_msgList({
          entity: {typeId: msgType.warnId},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_msgList({
          entity: {typeId: msgType.warnId},
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      processedHandle (msg) {
        this.currentMsg = msg
        this.processedDialogVisible = true
      },
      saveProcess () {
        if (this.form.problem.length > 1 && this.form.solution.length > 1) {
          this.ac_msgFeedback({
            id: this.currentMsg.id,
            bewrite: this.form.problem,
            programme: this.form.solution
          })
        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
        }
      },
      detailHandle (msg) {
        this.currentMsg = msg
        this.msgDetailDialogVisible = true
      },
      closeDialog () {
        this.processedDialogVisible = false
        this.msgDetailDialogVisible = false
        this.form = {
          problem: '',
          solution: '',
        }
        this.currentMsg = {}
      }
    },
    components: {
      contentTitle,
      contentC,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_msgList({
        entity: {typeId: msgType.warnId},
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/sprites/sprites.css";
  @import "../../../styles/common";

  .list {
    display: flex;
    font-size: 12px;
    flex-wrap: wrap;
    .item {
      min-width: 430px;
      display: flex;
      justify-content: space-between;
      margin-right: 10px;
      margin-bottom: 20px;
      padding: 20px;
      border-radius: 5px;
      .center {
        width: 340px;
        padding-left: 5px;
      }
      .time {
        font-weight: bold;
        font-size: 13px;
      }
      .cont {
        font-size: 12px;
        color: #8F8B8B;
      }
      .done {
        padding: 2px 10px;
        border: 1px solid #DE1515;
        color: #DE1515;
        border-radius: 5px;
        height: 20px;
      }
      .detail {
        padding: 2px 10px;
        border: 1px solid #4294E7;
        color: #4294E7;
        border-radius: 5px;
        height: 20px;
        width: 36px;
        text-align: center;
      }
      &.warn-bg {
        background-color: #FDEFEE;
        border: 1px solid #FBDCDC;
      }
      &.suc-bg {
        background-color: #E5EDF8;
        border: 1px solid #BAD7F6;
      }
    }
  }

  .t-1 {
    background-color: #45C5C0;
  }

  .x-color-1 {
    color: #45C5C0;
  }

  .t-2 {
    background-color: #DB0000;
  }

  .x-color-2 {
    color: #DB0000;
  }

  .msg-dialog {
    font-size: 12px;
    padding: 0 30px;
    h3 {
      margin: 20px 0 10px 0;
      color: #767676;
    }
    p {
      text-indent: 24px;
      color: #b2b2b2;
    }
  }
</style>



